.game-container {
  padding: 20px;
}

.game-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.95);
  padding: 15px 25px;
  border-radius: 15px;
  margin-bottom: 20px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

.room-info h3 {
  margin: 0;
  color: #333;
}

.room-info p {
  margin: 5px 0;
  color: #666;
}

.waiting-message {
  color: #f39c12 !important;
  font-weight: bold;
}

.leave-btn {
  background: #e74c3c;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s;
}

.leave-btn:hover {
  background: #c0392b;
  transform: translateY(-2px);
}

.loading-container, .error-container {
  text-align: center;
  padding: 50px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  margin: 50px auto;
  max-width: 500px;
}

.share-info {
  background: rgba(255, 255, 255, 0.95);
  padding: 25px;
  border-radius: 15px;
  margin-top: 30px;
  text-align: center;
}

.share-link {
  display: flex;
  gap: 10px;
  margin-top: 15px;
}

.share-link input {
  flex: 1;
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-family: monospace;
  font-size: 14px;
}

.share-link button {
  background: #27ae60;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
}

.share-link button:hover {
  background: #229954;
}

.turn-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 20px 40px;
  border-radius: 15px;
  font-size: 1.2rem;
  font-weight: bold;
  z-index: 10;
}

.game-board.disabled {
  position: relative;
  opacity: 0.7;
}

.player.you {
  border: 3px solid #27ae60;
}

@media (max-width: 768px) {
  .game-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .share-link {
    flex-direction: column;
  }
}