.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  padding: 20px;
}

.home-content {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  width: 100%;
}

.home-content h2 {
  color: #333;
  margin-bottom: 40px;
  font-size: 2rem;
}

.room-options {
  display: flex;
  gap: 40px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.option-card {
  background: #f8f9fa;
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  flex: 1;
  min-width: 250px;
}

.option-card h3 {
  color: #333;
  margin-bottom: 10px;
}

.option-card p {
  color: #666;
  margin-bottom: 20px;
}

.divider {
  font-size: 1.2rem;
  color: #999;
  font-weight: bold;
}

.create-btn, .join-btn {
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 12px 30px;
  font-size: 1rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: bold;
  width: 100%;
}

.create-btn:hover, .join-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.room-input {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 1rem;
  text-align: center;
  text-transform: uppercase;
}

.room-input:focus {
  outline: none;
  border-color: #667eea;
}

@media (max-width: 768px) {
  .room-options {
    flex-direction: column;
  }
  
  .divider {
    margin: 20px 0;
  }
}